const SET_AFFIDAVIT = 'active/SET_AFFIDAVIT';


export default function reducer(state = {}, action = {}) {
  switch (action.type) {
    case SET_AFFIDAVIT: return {
      ...state,
      affidavit: action.payload,
    };

    default: return state;
  }
}


export function setActiveAffidavit(value) {
  return {
    type: SET_AFFIDAVIT,
    payload: value,
  };
}


