const DELETE = 'affidavits/DELETE';
const ADD = 'affidavits/ADD'


const blankAffidavit = {
  personal: {
    basic: {
      firstName: '',
      lastName: '',
      age: '',
      gender: null,
    },
    language: {
      speaking: null,
      reading: null,
      writing: null,
      homeLanguage: null,
    },
    citizenship: {
      citizen: null,
      other: null,
    },
    health: {
      problems: null,
      list: [],
    },
    employment: {
      currently: null,
      type: null,
      suburb: null,
      month: null,
      year: null,
    }
  },
  occupants: {}
}


export default function reducer(state = {}, action = {}) {
  switch (action.type) {
    case DELETE: return {
      ...state.list.filter(({ id }) => id !== action.payload),
    };

    case ADD: return {
      ...state,
      [action.payload]: blankAffidavit,
    };

    default: return state;
  }
}


export function deleteAffidavitWithId(id) {
  return {
    type: DELETE,
    payload: id,
  };
}

export function addAffidavitWithId(id) {
  if (!id) {
    return null;
  }

  return {
    type: ADD,
    payload: id,
  };
}


